import React, { Component } from "react";
import { logout } from "../helpers/helper";
import avatar from "../image/avatar.png";
import DropDownMenu from "./DropDownMenu";

"use strict";

const initialState = { showDropDown: false };
type State = Readonly<typeof initialState>;
type Props = {
  refreshLoginStatus: Function;
  currentUser: string;
  logged: boolean;
  style: object;
  onDarkModeSwitchClick: Function;
};
export default class Navigation extends Component<Props, State> {
  readonly state: State = initialState;
  handleLogoutClick = e => {
    logout(e);
    setTimeout(() => {
      this.props.refreshLoginStatus();
    }, 1000);
  };
  toggleDropDownMenu = e => {
    this.setState(prevState => ({
      showDropDown: !prevState.showDropDown
    }));
  };
  componentDidMount = () => {
    this.props.refreshLoginStatus();
  };

  render() {
    const { showDropDown } = this.state;
    const { currentUser, logged, style, onDarkModeSwitchClick } = this.props;
    return (
      <div className="wrapper-navbar">
        <a className="logo" href="/studywell">
          Study Well
        </a>
        <div className="navbar">
          <button
            className={style["btn"]}
            onClick={() => onDarkModeSwitchClick()}
          >
            Light mode / Dark mode
          </button>
          {true ? (
            <button
              className={style["btn"]}
              // onClick={this.onshowAllQuestionsClick}
            >
              Hide all questions
            </button>
          ) : (
            <button
              className={style["btn"]}
              // onClick={this.onshowAllQuestionsClick}
            >
              Show all questions
            </button>
          )}
          <a href="/studywell/groups" className={style["btn"]}>
            Groups
          </a>
          {/* <button className={style["btn"]}>Shuffle</button>
          <button className={style["btn"]}>Add Question</button> */}
          {logged ? null : (
            <a href="/studywell/login" className={style["btn"]}>
              Login
            </a>
          )}
          {!logged && <button className={style["btn"]}>Sign up</button>}

          {logged && (
            <div className="wrapper--user">
              <img
                className="avatar"
                src={avatar}
                alt="avatar"
                onClick={this.toggleDropDownMenu}
              />
              <DropDownMenu
                showDropDown={showDropDown}
                currentUser={currentUser}
                handleLogoutClick={this.handleLogoutClick}
              />
            </div>
          )}
        </div>
      </div>
    );
  }
}
